import { CHOOSE_FILM, CHOOSE_CINEMA, CHOOSE_DATE, CHOOSE_TIME} from '../action/type.js'

const initialState = {
	film:{
		'Ch Mi Ba: 3 Ngy Sinh T':{
			'Cinestar Bnh Dng':{
				'Hm nay':['22:30','23:46']
			},
			'Lotte Cinema Bnh Dng': {
				'Hm nay':['Khng c sut chiu']
			},
			'BHD Star Discovery': {
				'Hm nay':['Khng c sut chiu'],
				'Ngy mai': ['16:50','22:05'],
				'Th su': ['Khng c sut chiu'],
				'Th by': ['Khng c sut chiu'],
				'Ch nht': ['Khng c sut chiu'],
				'Th hai': ['Khng c sut chiu'],
			},
			'Beta Cinemas M nh': {
				'Hm nay':['Khng c sut chiu'],
				'Ngy mai':['Khng c sut chiu'],
				'Th su': ['13:45'],
				'Th by': ['13:45'],
				'Ch nht': ['13:45'],
				'Th hai': ['13:45'],
				'Th ba': ['13:45'],
			},
			'Beta Cinemas an Phng': {
				'Hm nay':['Khng c sut chiu'],
				'Ngy mai':['Khng c sut chiu'],
				'Th su': ['13:45'],
				'Th by': ['13:45'],
				'Ch nht': ['13:45'],
				'Th hai': ['13:45'],
				'Th ba': ['13:45'],
			},
			'Lotte Cinema H ng': {
				'Hm nay':['Khng c sut chiu'],
				'Ngy mai':['Khng c sut chiu'],
				'Th su': ['13:45'],
				'Th by': ['09:15','11:15','16:00','18:30','20:15'],
				'Ch nht': ['13:45'],
				'Th hai': ['13:45'],
				'Th ba': ['13:45'],
			}
		},
		'La u Gp La o - The Con-Heartist (C16)': {

		},
		'Th Sn Qui Vt - Monster Hunter (C13)': {

		},
		'Cuc Sng Nhim Mu - Soul - (P)': {

		},
		'Sm Hi (C18)': {

		}
	},
	current: {
		'film':'Phim',
		'cinema':'Rp',
		'date':'Ngy xem',
		'time':'Sut chiu'
	}
}

const chooseFilmReducer = (state = initialState,action) => {
	switch(action.type){
		case CHOOSE_FILM:{
			let newState = {...state,current:{
				...state.current,'film':action.payload
			}};
			return newState;
		}
		case CHOOSE_CINEMA:{
			let newState = {...state,current:{
				...state.current,'cinema':action.payload
			}};
			return newState;
		}
		case CHOOSE_DATE:{
			let newState = {...state,current:{
				...state.current,'date':action.payload
			}};
			return newState;
		}
		case CHOOSE_TIME:{
			let newState = {...state,current:{
				...state.current,'time':action.payload
			}};
			return newState;
		}
		default:
			return state;
	}
}

export default chooseFilmReducer;


