import { CHOOSE_SITE } from '../action/type.js';

const initialState = {
	site: 'H Ni',
	sites: ['H Ni', ' Nng','H Ch Minh','Hi Phng','Bin Ha','Nha Trang','Bnh Dng','Phan Thit','H Long','Cn Th','Vng Tu','Quy Nhn','Hu','Long Xuyn','Thi Nguyn','Bun Ma Thut','Bc Giang','Bn Tre','Vit Tr','Ninh Bnh','Thi Bnh','Vinh','Bo Lc',' Lt','Tr Vinh','Yn Bi','Kin Giang','Vnh Long',
	'Hu Giang','Ty Ninh','Tuyn Quang','Thanh Ha','Nam nh','Hi Dng','Gia Lai','H Tnh','Ph Yn']
}


const siteReducer = (state = initialState, action) => {
	switch(action.type) {
		case CHOOSE_SITE:{
			const newState = {...state,site:action.payload};
			return newState;
		}
		default:
			return state;
	}
}

export default siteReducer;


